.App {
  text-align: center;
}

@import url("https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;500&display=swap");
.ContainerInfoPrincipaltext>p{
  font-family: 'Dancing Script', 'cursive';
    padding: 0;
    margin: 0;
    font-size: 30px;
    color:#a34228 ;
}
.NavMap>p{
  color:#a34228 ;
}
.NavMap>p:hover{
  color:#FCF2CE ;
}
.ContainerInfoHome{
  background-image: url("https://res.cloudinary.com/dfmgas1qy/image/upload/v1696267842/sunset-sky-plants-field-sunrise-morning-53384-wallhere.com.jpg.jpg");
  width: 100%;
  height:100vh; 
  justify-content: center;
  align-items: center;
  background-size: cover;
}
.ContainerInfoPrincipal{

  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.ContainerHeader{
  padding-top: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.ContainerNav{
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100px;

}
.ContainerIcon{
  width:20%;
}
.NavMap{
  display: flex;
  flex-direction: row;
  margin-left: 20%;
  width: 80%;
}
.NavMap>p{
  padding: 10px;
}
.IconNyF{
  width: 50%;
  height: auto;
  
}

.imgLogo{
  width: 100%;
  height: auto;
}